
<template>
<div class="orderhistory">
    <el-input placeholder="请输入订单号进行搜索" v-model="OrderId" class="input-with-select" style="width:40%;">
    <el-button slot="append" icon="el-icon-search" @click="orderSearch"></el-button>
    </el-input>
    <el-table :data="listData" size="small" border  style="margin-top:20px"
    :default-sort = "{prop: 'date', order: 'descending'}" v-loading="loading">
        <el-table-column align="center" prop="OrderCode" label="订单号" sortable></el-table-column>
        <el-table-column align="center" prop="Price" label="价格" sortable></el-table-column>
        <el-table-column align="center" prop="PayType" label="购买方式" sortable></el-table-column>
        <el-table-column align="center" prop="OrderState" label="订单状态" sortable></el-table-column>
        <el-table-column align="center" prop="CreateDate" label="创建日期" sortable></el-table-column>
        <el-table-column align="center" label="操作" width="100">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="orderDeit(scope.row.OrderId)">详情</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog
    title="订单详情"
    :visible.sync="orderDeitDialog"
    width="50%">
    <el-form ref="form" :model="form" label-width="80px">
        <el-row>
            <el-col :span="12">
                <el-form-item label="订单号:">
                    <span>{{form.OrderCode == null?'暂无':form.OrderCode}}</span>
                </el-form-item>
                <el-form-item label="保证金:">
                    <span>{{form.Bond == null?'暂无':form.Bond}}</span>
                </el-form-item>
                <el-form-item label="原价:">
                    <span>{{form.OriginalPrice == null?'暂无':form.OriginalPrice}}</span>
                </el-form-item>
                <el-form-item label="成交价:">
                    <span>{{form.Price == null?'暂无':form.Price}}</span>
                </el-form-item>
                <el-form-item label="支付方式:">
                    <span>{{form.PayType == null?'暂无':form.PayType}}</span>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="所属楼盘:">
                    <span>{{form.BuildingName == null?'暂无':form.BuildingName}}</span>
                </el-form-item>
                <el-form-item label="车位类型:">
                    <span>{{form.ParkingTypeName == null?'暂无':form.ParkingTypeName}}</span>
                </el-form-item>
                <el-form-item label="订单状态:">
                    <el-button type="primary" size='mini'>{{form.OrderState == 3?'已交付':form.OrderState == 4?'申请回购':form.OrderState == 5?'已回购':form.OrderState == -1?'退订':form.OrderState == -2?'已超时':form.OrderState == -3?'已删除':'暂无'}}</el-button>
                </el-form-item>
                <el-form-item label="订单类型:">
                    <span>{{form.Type == null?'暂无':form.Type}}</span>
                </el-form-item>
            </el-col>
        </el-row>
        <el-form-item label="备注:">
            <span>{{form.Description}}</span>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="orderDeitDialog = false">取 消</el-button>
        <el-button type="primary" @click="orderDeitDialog = false">确 定</el-button>
    </span>
    </el-dialog>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.rows" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'OrderHistory',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            form: {
                Bond: '',
                BuildingName: '',
                OrderCode: '',
                OrderState: '',
                OriginalPrice: '',
                Price: '',
                ParkingTypeName: '',
                Type: '',
                Description: '',
            },
            orderDeitDialog:false,
            listData: [],
            loading: true,
            OrderId:''
        };
    },
    methods: {
        getData(){
            // let objQuery = {}
            // objQuery.OrderId=''
            // objQuery.OrderState="0"
            this.loading = true
            this.$api.userInfoApi.MyOrder(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    queryJson:JSON.stringify({OrderId:this.OrderId,OrderState:"3,-1,-2,-3,4,5"}),
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('数据异常')
                }
            }).catch(error=>{
                this.$message.error('暂无数据')
                this.loading = false
            })
        },
        orderDeit(id){
            this.orderDeitDialog = true
            // MyOrderDetails
            this.$api.userInfoApi.MyOrderDetails(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    orderId:id
                }
            ).then(res=>{
                console.log(res)
                this.form = res.data.data.data[0]
                
            }).catch(error=>{
            })
        },
        orderSearch(){
            this.getData()
        },
        setPage(val) {
            this.listQuery.page = val
            this.onLoad()
        },
        setSize(val) {
            this.listQuery.size = val
            this.onLoad()
        },
        detailsopen(){
        },
        add(){
        },
        formatter(row, column) {
            return row.address;
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.orderhistory .pagination-container{
    margin-top:20px;
    text-align: center;
}
</style>