<template>
 <bm-overlay
    ref="customOverlay"
    pane="labelPane"
    
    @draw="draw"
    
    >
    <!-- v-for="item in rows"
    :key="item.BuildingId" -->
    
    <div>
        <p style="margin:12px 0 0 0;">{{itemcity}}</p>
        <p style="margin:10px 0 0 0;">{{citymap.card}}个车位</p>
    </div>
</bm-overlay>
</template>

<script>
export default {

  props: ['position','itemcity'],
  data(){
    return{
      yuanShow:true,
      citymap:{
          District:'北京市',
          card:'55'
      },
      
    }
  },
  watch: {
    position: {
      handler () {
        this.$refs.customOverlay.reload()
      },
      deep: true
    }
  },
  methods: {
    
    draw ({el, BMap, map}) {
      const {lng, lat} = this.position
      const pixel = map.pointToOverlayPixel(new BMap.Point(lng, lat))
      el.style.left = pixel.x - 60 + 'px'
      el.style.top = pixel.y - 20 + 'px'
    //   console.log(map.Oa)
    //   if(13<=map.Oa||map.Oa<=10){
    //       this.yuanShow = false
    //       this.$nextTick(function () {
    //           this.yuanShow = false
    //       });
    //   }else{
    //       this.yuanShow = true
    //       this.$nextTick(function () {
    //           this.yuanShow = true
    //       });
    //   }
    }
  }
}
</script>

