
<template>
<div class="mycarport">
    <el-table :data="listData" size="small" border  style="margin-top:20px;width:100%;"  v-loading="loading"
    :default-sort = "{prop: 'date', order: 'descending'}" @sort-change='dataSort(column, prop, order)'>
        <el-table-column align="center" prop="OrderCode" label="订单号" sortable='custom' ></el-table-column>
        <el-table-column align="center" prop="ParkingCode" label="车位编号" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="BuildingName" label="所属小区" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="Price" label="车位价格" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="FixedRate" label="收益利率" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="EnsureMark" label="使用权益" ortable='custom'></el-table-column>
        <el-table-column align="center" prop="RepoTerm" label="有效期" ortable='custom'></el-table-column>
        <el-table-column align="center" label="操作" width="450" fixed="right">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="carportEdit(scope.row.ParkingId)">详情</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">转让</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">打款赎单</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">质押融资</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">申请回购</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog
    title="订单详情"
    :visible.sync="cardDeitDialog"
    width="50%">
    <el-form ref="form" :model="form" label-width="80px">
        <el-row>
            <el-col :span="12">
                <el-form-item label="车位面积:">
                    <span>{{form.Acreage == null?'暂无':form.Acreage}}</span>
                </el-form-item>
                <el-form-item label="联系人:">
                    <span>{{form.Contact == null?'暂无':form.Contact}}</span>
                </el-form-item>
                <el-form-item label="联系人电话:">
                    <span>{{form.ContactTel == null?'暂无':form.ContactTel}}</span>
                </el-form-item>
                <el-form-item label="回购说明:">
                    <span>{{form.Instruction == ''?'暂无':form.Instruction}}</span>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="所在位置:">
                    <span>{{form.Location == null?'暂无':form.Location}}</span>
                </el-form-item>
                <el-form-item label="所有人:">
                    <span>{{form.OwnnerId == null?'暂无':form.OwnnerId}}</span>
                </el-form-item>
                <el-form-item label="车位编号:">
                    <span>{{form.ParkingCode == null?'暂无':form.ParkingCode}}</span>
                </el-form-item>
                <el-form-item label="车位类型:">
                    <span>{{form.ParkingType == null?'暂无':form.ParkingType}}</span>
                </el-form-item>
                <el-form-item label="是否质押:">
                    <span>{{form.PledgeMark == null?'暂无':form.PledgeMark}}</span>
                </el-form-item>
                <el-form-item label="成交价:">
                    <span>{{form.Price == null?'暂无':form.Price}}</span>
                </el-form-item>
                <el-form-item label="协议模板名称:">
                    <span>{{form.ProtocolName == null?'暂无':form.ProtocolName}}</span>
                </el-form-item>
                <el-form-item label="车位状态:">
                    <span>{{form.State == 0?'待上架':form.State == 1?'申请上架，待审批':form.State == 2?'审批通过，已上架':form.State == -1?'审批未通过，退回':form.State == 3?'询价中':form.State == 4?'售出':form.State == 5?'发起回购':form.State == 6?'已回购':'暂无'}}</span>
                </el-form-item>
               
                <el-form-item label="是否银行回购保证:">
                    <el-button type="primary" size='mini'>{{form.EnsureMark == 1?'保证回购':form.EnsureMark == -1?'无回购保证':'暂无'}}</el-button>
                </el-form-item>
                <el-form-item label="订单类型:">
                    <span>{{form.Type == null?'暂无':form.Type}}</span>
                </el-form-item>
            </el-col>
        </el-row>
        <el-form-item label="备注:">
            <span>{{form.Description}}</span>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="orderDeitDialog = false">取 消</el-button>
        <el-button type="primary" @click="orderDeitDialog = false">确 定</el-button>
    </span>
    </el-dialog>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.size" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'MyCarport',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            form: {
                Acreage: '',
                Contact: '',
                ContactTel: '',
                Instruction: '',
                Location: '',
                OwnnerId: '',
                ParkingCode: '',
                ParkingType: '',
                PledgeMark: '',
                Price: '',
                ProtocolName: '',
                State: '',
                EnsureMark: '',
            },
            cardDeitDialog:false,
            listData: [],
            loading: true,
        };
    },
    methods: {
        getData(){
            let objQuery = {}
            objQuery.ParkingCode=''
            objQuery.state='0,1,2,3,4,5,6'
            this.$api.userInfoApi.ParkingInfo(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    queryJson:JSON.stringify(objQuery),
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('数据异常')
                }
            }).catch(error=>{
                this.$message.error('暂无数据')
                this.loading = false
            })
        },
        dataSort(column, prop, order){
            console.log(column, prop, order)
        },
        setPage(val) {
            this.listQuery.page = val
            this.getData()
        },
        setSize(val) {
            this.listQuery.size = val
            this.getData()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        },
        carportEdit(id){
            // console.log(row.row.id)
            //  this.$router.push({
            //     path: '/home',
            //     query: {
            //         id: row.row.id
            //     }
            // })
            this.cardDeitDialog = true
            console.log(id)
            this.loading=true
            this.$api.userInfoApi.ParkingDetail(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    parkingId:id
                }
            ).then(res=>{
                console.log(res)
                this.loading = false
                // this.form = res.data.data.data[0]
                
            }).catch(error=>{
            })
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.mycarport .pagination-container{
    margin-top:20px;
    text-align: center;
}
</style>