
<template>
<div class="unpaid">
    <el-input placeholder="请输入订单号进行搜索" v-model="OrderId" class="input-with-select" style="width:40%;">
    <el-button slot="append" icon="el-icon-search" @click="orderSearch"></el-button>
    </el-input>
    <el-table :data="listData" size="small" border  style="margin-top:20px"
    :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column align="center" prop="OrderCode" label="订单号" sortable></el-table-column>
        <el-table-column align="center" prop="Price" label="金额" sortable></el-table-column>
        <el-table-column align="center" prop="Instruction" label="车位信息" sortable></el-table-column>
        <el-table-column align="center" label="操作" width="350">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="orderDeit(scope.row.OrderId)">详情</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">立即支付</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">贷款购买</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">取消订单</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-dialog
    title="订单详情"
    :visible.sync="orderDeitDialog"
    width="50%">
    <el-form ref="form" :model="form" label-width="80px">
        <el-row>
            <el-col :span="12">
                <el-form-item label="订单号:">
                    <span>{{form.OrderCode}}</span>
                </el-form-item>
                <el-form-item label="保证金:">
                    <span>{{form.Bond}}</span>
                </el-form-item>
                <el-form-item label="原价:">
                    <span>{{form.OriginalPrice}}</span>
                </el-form-item>
                <el-form-item label="成交价:">
                    <span>{{form.Price}}</span>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="所属楼盘:">
                    <span>{{form.BuildingName}}</span>
                </el-form-item>
                <el-form-item label="车位类型:">
                    <span>{{form.ParkingTypeName}}</span>
                </el-form-item>
                <el-form-item label="订单状态:">
                    <el-button type="danger" size='mini'>未支付</el-button>
                </el-form-item>
                <el-form-item label="订单类型:">
                    <span>{{form.Type}}</span>
                </el-form-item>
            </el-col>
        </el-row>
        <el-form-item label="备注:">
            <span>{{form.Description}}</span>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="orderDeitDialog = false">取 消</el-button>
        <el-button type="primary" @click="orderDeitDialog = false">确 定</el-button>
    </span>
    </el-dialog>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.size" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'UnPaid',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                // rows:10,
                page: 1,
                rows: 10,
                total: 1,
            },
            form: {
                Bond: '',
                BuildingName: '',
                OrderCode: '',
                OrderState: '',
                OriginalPrice: '',
                Price: '',
                ParkingTypeName: '',
                Type: '',
                Description: '',
            },
            orderDeitDialog:false,
            listData: [],
            loading: true,
            OrderId:''
        };
    },
    methods: {
        getData(){
            this.loading = true
            this.$api.userInfoApi.MyOrder(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    queryJson:JSON.stringify({OrderId:this.OrderId,OrderState:"0"}),
                    data:JSON.stringify(this.listQuery)
                }
            ).then(res=>{
                if(res.data.code=='200'){
                    this.listData = res.data.data.rows
                    this.listQuery.total = res.data.data.records
                    this.loading = false
                }
                if(res.data.code=='400'){
                    this.loading = true
                    this.$message.error('数据异常')
                }
            }).catch(error=>{
                this.$message.error('暂无数据')
                this.loading = false
            })
        },
        orderSearch(){
            // let objQuery = {}
            // objQuery.OrderId=this.OrderId
            this.getData()
        },
        setPage(val) {
            this.listQuery.page = val
            this.onLoad()
        },
        orderDeit(id){
            this.orderDeitDialog = true
            // MyOrderDetails
            this.$api.userInfoApi.MyOrderDetails(
                {
                    LoginMark:'1234567890',
                    Token:localStorage.token,
                    orderId:id
                }
            ).then(res=>{
                console.log(res)
                this.form = res.data.data.data[0]
            }).catch(error=>{
                this.$message.error('暂无数据')
                this.loading = false
            })
        },
        setSize(val) {
            this.listQuery.size = val
            this.onLoad()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        }
    },
    created() {
        this.getData()
    }
  }
</script>
<style scoped>
.unpaid .pagination-container{
    margin-top:20px;
    text-align:center
}
</style>